import React, {Component, Fragment} from 'react';
import {CSSTransition} from 'react-transition-group';
import {connect} from 'react-redux';
import {Link} from "react-router-dom";
import {actionCreators} from './store';
import {actionCreators as logOut} from '../../pages/login/store';
import {
  HeaderWrapper,
  Logo,
  Nav,
  NavItem,
  NavSearch,
  Addition,
  Button,
  SearchWrapper,
  SearchInfo,
  SearchInfoTitle,
  SearchInfoSwitch,
  SearchInfoList,
  SearchInfoItem,
} from './style';

const LoginView = (props) => {
  return (
    <Fragment>
      <Link to="/login">
        <Button className="login">登录</Button>
      </Link>
      <Button className="reg">注册</Button>
    </Fragment>
  )
};

const LoginEnd = (props) => {
  return (
    <Fragment>
      <span className="username">{props.content}</span>
      <Button className="logOut" onClick={props.logout}>退出</Button>
    </Fragment>
  )
};

class Header extends Component {
  render() {
    const {focused, handleInputFocus, handleInputBlur, login, username, logout} = this.props;
    return (
      <HeaderWrapper>
        <Nav>
          <Logo/>
          <NavItem className="left active">首页</NavItem>
          <NavItem className="left">发现</NavItem>
          <NavItem className="left">话题</NavItem>
          <SearchWrapper className="left">
            <CSSTransition in={focused} timeout={200} classNames="slide">
              <NavSearch
                className={focused ? 'focused' : ''}
                // 避免重复ajajx请求
                onFocus={() => handleInputFocus(this.props.list)}
                onBlur={handleInputBlur}
              />
            </CSSTransition>
            <i className={focused ? 'focused iconfont zoom' : 'iconfont zoom'}>
              &#xe661;
            </i>
            {this.getListArea(focused)}
          </SearchWrapper>
          <Addition>
            {
              login ? <LoginEnd content={username} logout={logout}/> : <LoginView/>
            }
          </Addition>
        </Nav>
      </HeaderWrapper>
    );
  }

  getListArea(show) {
    const {list, page, totalPage, handleMouseEnter, handleMouseLeave, mouseIn, handleChange} = this.props;
    const newList = list.toJS();
    const pageList = [];
    if (newList.length) {
      for (let i = (page - 1) * 10; i < page * 10; i++) {
        pageList.push(
          <SearchInfoItem key={newList[i]}>{newList[i]}</SearchInfoItem>
        )
      }
    }
    if (show || mouseIn) {
      return (
        <SearchInfo
          onMouseEnter={handleMouseEnter}
          onMouseLeave={handleMouseLeave}>
          <SearchInfoTitle>
            热门搜索
            <SearchInfoSwitch onClick={() => {
              handleChange(page, totalPage, this.spinIcon)
            }}>
              <i ref={(icon) => {
                this.spinIcon = icon
              }} className="iconfont spin">&#xe851;</i>
              <span>换一批</span>
            </SearchInfoSwitch>
          </SearchInfoTitle>
          <SearchInfoList>
            {pageList}
          </SearchInfoList>
        </SearchInfo>
      )
    } else {
      return null;
    }
  }
}

const mapStateToProps = (state) => {
  return {
    focused: state.getIn(["header", "focused"]),
    list: state.getIn(["header", "list"]),
    page: state.getIn(["header", "page"]),
    totalPage: state.getIn(["header", "totalPage"]),
    mouseIn: state.getIn(["header", "mouseIn"]),
    login: state.getIn(["login", "login"]),
    username: state.getIn(["login", "username"])
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    handleInputFocus(list) {
      if (list.size === 0) {
        dispatch(actionCreators.getList());
      }
      dispatch(actionCreators.searchFcous());
    },
    handleInputBlur() {
      dispatch(actionCreators.searchBlur());
    },
    handleMouseEnter() {
      dispatch(actionCreators.mouseEnter());
    },
    handleMouseLeave() {
      dispatch(actionCreators.mouseLeave());
    },
    handleChange(page, totalPage, spin) {
      let originAngle = spin.style.transform.replace(/[^0-9]/ig, "");
      console.log(originAngle);
      if (originAngle) {
        originAngle = parseInt(originAngle, 10);
      } else {
        originAngle = 0;
      }
      spin.style.transform = 'rotate(' + (originAngle + 360) + 'deg)';
      if (page < totalPage) {
        dispatch(actionCreators.changePage(page + 1));

      } else {
        dispatch(actionCreators.changePage(1))

      }
    },
    logout() {
      dispatch(logOut.logout())
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Header);
