import styled from 'styled-components';
import logo from '../../static/images/logo.jpg';

export const HeaderWrapper = styled('div')`
  height: 52px;
  border-bottom: 1px solid #f0f0f0;
`;
export const Nav = styled('div')`
  position: relative;
   width: 1032px;
   height: 100%;
   margin: 0 auto;
   padding-right: 70px;
   box-sizing: border-box;
`;
export const Logo = styled('a').attrs({
  href: '/',
})`
float: left;
  width: 100px;
  height: 50px;
  background: url(${logo});
  background-size: contain;
`;

export const NavItem = styled('a')`
  color: #8590a6;
  line-height: 52px;
  padding: 0 15px;
  font-size: 15px;
  cursor: pointer;
  &.left {
    float: left;
  }
  &.right {
    float: right;
  }
  &.active {
    color: #444;
  }
  &:hover{
    color: #444;
  }
`;

export const SearchWrapper = styled("div")`
    position: relative;
    float: left;
    .zoom {
      position: absolute;
      right:0px;
      bottom: 0px;
      width: 30px;
      line-height: 30px;
      border-radius: 8px;
      text-align: center;
      &.focused{
      background: dodgerblue;
      color: #fff;
      }
    }
`;

export const NavSearch = styled('input').attrs({
  placeholder: '搜索',
})`
   width: 180px;
   height: 30px;
   line-height: 30px;
   padding: 0 35px 0px 15px;
   margin-left: 20px;
   margin-top: 12px;
   box-sizing: border-box;
   border: none;
   outline: none;
   border-radius: 8px;
   background: #ebebeb;
   font-size: 14px;
   &::placeholder {
    color: #999;
   }
   &.focused{
    width: 260px;
   }
   &.slide-enter {
      width: 180px;
      transition: all .2s ease-out;
    }
    &.slide-enter-active {
      width: 260px;
    }
    &.slide-exit{
      transition: all .2s ease-out;
    }
    &.slide-exit-active{
      width: 180px;
    }
`;

export const SearchInfo = styled("div")`
  position:absolute;
  left:20px;
  top:52px;
  width:240px;
  min-height:100px;
  padding:0 10px;
  box-shadow: 0 0 8px rgba(0,0,0,.2);
  z-index: 100;
  background: #fff;
  i {
    margin-top: 1px;  
  }
  .spin{
    display: block;
    float: left;
    font-size: 12px;
    margin-right: 2px;
    transition: all 1s ease-in;
    transform-origin: center center;
  }
`;

export const SearchInfoTitle = styled("div")`
  margin-top:5px;
  margin-bottom:15PX;
  line-height:20px;
  color:#969696;
  font-size:14px;
`;

export const SearchInfoSwitch = styled("span")`
  float:right;
  font-size:12px;
  span{
    cursor: pointer;
  }
`;

export const SearchInfoList = styled("div")`
   overflow:hidden;
`;

export const SearchInfoItem = styled("a")`
  display:block;
  float:left;
  margin-right:10px;
  margin-bottom:10px;
  padding:0 5px;
  line-height:20px;
  font-size:12px;
  border:1px solid #ddd;
  color:#787878;
  border-radius:3px;
`;

export const Addition = styled("div")`
  position:absolute;
  right:0;
  top:0;
  height:52px;
  .username{
      font-size: 14px;
      line-height: 14px;
      margin-top: 20px;
      float: left;
  }
`;

export const Button = styled("div")`
  float: right;
  margin-top: 9px;
  margin-right: 20px;
  padding: 0 20px;
  line-height: 38px;
  border-radius: 19px;
  font-size: 14px;
  cursor: pointer;
  &.reg{
  color: #8590a6;
  }
  &.login{
  color: #fff;
  background: #0084ff;
  }
`;
