import styled from 'styled-components';

export const HomeWrapper = styled("div")`
    width: 1032px;
    margin: 0 auto;
    padding: 10px;
`;

export const ListItem = styled("div")`
    margin-top: 20px;
    overflow:hidden;
    padding: 10px 10px 0;
    border-bottom: 1px solid #dedede;
    .pic{
      display: block;
      margin-top: 15px;
      width: 85px;
      height: 85px;
      float: right;
      border-radius: 10px;
    }
`;


export const ListInfo = styled("div")`
    width: 890px;
    float: left;
    .title{
      line-height: 19px;
      font-size: 19px;
      font-weight: bold;
      color: #333;
    }
    .desc{
      line-height: 24px;
      font-size: 13px;
      color: #999;
    }
`;

export const BackTop = styled("div")`
   position: fixed;
   right: 30px;
   bottom: 30px;
   padding: 10px;
   text-align: center;
   border: 1px solid #dedede;
   cursor: pointer;
   font-size: 12px;
`;

export const LoadMore = styled("div")`
    width: 100%;
    height: 40px;
    margin: 30px 0;
    line-height: 40px;
    text-align: center;
    border-radius: 15px;
    color: #fff;
    background: #a5a5a5;
    cursor: pointer;
`;
