import React, {Component, Fragment} from "react"
import {connect} from "react-redux";
import {ListItem, ListInfo, LoadMore} from "../style"
import {actionCreators} from "../store"
import {Link} from "react-router-dom"


class HomeCarousel extends Component {
  render() {
    const {info, getMoreList, page} = this.props;
    return (
      <Fragment>
        {
          info.map((item, index) => {
            return (
              // <ListItem key={item.get("id")}>
              <Link key={index} to={"/detail/" + item.get("id")}>
                <ListItem>
                  <img className="pic" src={item.get("imgUrl")} alt=""/>
                  <ListInfo className="info_detail">
                    <h3 className="title">{item.get("title")}</h3>
                    <p className="desc">{item.get("content")}</p>
                  </ListInfo>
                </ListItem>
              </Link>
            )
          })
        }
        <LoadMore onClick={() => getMoreList(page)}>
          更多
        </LoadMore>
      </Fragment>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    info: state.getIn(["home", "info"]),
    page: state.getIn(["home", "articlePage"]),
  };
};

const mapDispatch = (dispatch) => {
  return {
    getMoreList(page) {
      const action = actionCreators.getMoreList(page);
      dispatch(action)
    }
  }
};

export default connect(mapStateToProps, mapDispatch)(HomeCarousel);
