import React, {Component} from "react"
import {BackTop, HomeWrapper} from "./style"
import HomeCarousel from "./components/swiper"
import List from "./components/list"
import {connect} from "react-redux"
import {actionCreators} from "./store"



class Home extends Component {
  render() {
    const {handleTop} = this.props;
    return (
      <HomeWrapper>
        <HomeCarousel/>
        <List/>
        {
          this.props.showTop ? <BackTop onClick={handleTop}>回到顶部</BackTop> : null
        }
      </HomeWrapper>
    )
  }

  componentDidMount() {
    this.props.changeHomeData();
    this.bindEvents();
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.props.showTheTop)
  }

  bindEvents() {
    window.addEventListener("scroll", this.props.showTheTop)
  }
}


const mapStateToProps = (state) => {
  return {
    showTop: state.getIn(["home", "showTop"])
  };
};

const mapDispatchToProps = (dispatch) => ({
  changeHomeData() {
    const action = actionCreators.getHomeInfo();
    dispatch(action)
  },
  handleTop() {
    window.scrollTo(0, 0)
  },
  showTheTop() {
    let height = document.documentElement.scrollTop;
    if (height > 100) {
      dispatch(actionCreators.toggleTopShow(1))
    } else {
      dispatch(actionCreators.toggleTopShow(0))
    }
  }
});
export default connect(mapStateToProps, mapDispatchToProps)(Home);

