import {fromJS} from "immutable"
import {GET_ITEM_DATA, GET_MORE_DATA} from "./constants"

const defaultState = fromJS({
    imgItem: [],
    info: [],
    articlePage: 1,
    showTop: false
  })
;


export default (state = defaultState, action) => {
  switch (action.type) {
    case GET_ITEM_DATA:
      return state.merge({
        imgItem: fromJS(action.imgItem),
        info: fromJS(action.info)
      });
    case GET_MORE_DATA:
      return state.merge({
        info: state.get("info").concat(action.list),
        articlePage: action.nextPage
      });
    case "show_top":
      return state.set("showTop", action.show);
    default:
      return state;
  }
}
